import React, { Component } from 'react';
import iFacebook from './imagens/icons/fb_icon.png';
import iLinkedin from './imagens/icons/linkedin_icon.png';
import iWhatsapp from './imagens/icons/wp_icon.png';
import iGithubb from './imagens/icons/github.svg';
import SpeakBox from './imagens/barra/speakbox.svg';
/*Principal*/
const contatosCss = {
    width: '100%',
    height: '100%',
    position: 'absolute',
    overflowY: 'hidden'
};
/*Titulo */
const tituloClasse = ' text-light pl-3 pr-3 rounded text-left mt-0 mb-0 pt-0 pb-0 ';
const tituloCss = {
    marginTop: '30px',
    overflow: 'visible',
};
/*Barras*/
const barraClasse = "float-left mt-1 mb-1 barra-contato text-nowrap";
const barraCss = {
    clear: 'both',
    verticalAlign: 'middle',
    padding: '5px 10px 5px 0px',
    overflow: 'hidden',
    
};
const barraContainerCss = {

}

/*Logos*/
const logoCss = {
    height: '30px',
    overflow: 'hidden',
    position: 'absolute',
    left: '5px',
    marginTop: '8px'
}

const linkCss = {
    position: 'absolute',
    left: '40px',
    marginTop: '12px',
    fontSize:'80%'
}


class contatos extends Component {
    render() {
        return (
            <div style={contatosCss} className={` ${this.props.gradiente}`}>
                <div style={{ minHeight: '100%', overflow:'hidden',zIndex:'3'}} >
                    <img src={require('./imagens/iconespagina/' + this.props.icone + '.svg')} className='icone' style={{transform:'translateX(30px) translateY(-15px)'}}></img>


                    <div id='titulo' style={tituloCss}>
                        <h5 className={tituloClasse}>Contatos </h5>
                    </div>


                    <div className={barraClasse} style={barraCss}>
                        <div style={barraContainerCss} className='row'>
                            <svg width="270" height="60" viewBox="0 0 279 65" fill="none" xmlns="http://www.w3.org/2000/svg">
                                <path d="M-33.5 15C-33.5 6.99188 -27.0081 0.5 -19 0.5H264C272.008 0.5 278.5 6.99187 278.5 15V32.2285C278.5 40.2758 271.947 46.7838 263.9 46.7281L64.9944 45.3529C61.9799 45.332 59.0248 46.1907 56.4908 47.8237L32.6814 63.1678C34.6917 56.9797 30.4177 50.2592 23.6493 49.7403L-20.1084 46.3855C-27.6645 45.8062 -33.5 39.5061 -33.5 31.9279V15Z" fill="white" stroke="#C4C4C4" />
                            </svg>

                            <img src={iFacebook} style={logoCss}></img><a style={linkCss} className='ml-2' href='https://www.facebook.com/devalanreis'>facebook.com/devalanreis</a>
                        </div>
                    </div>

                    <div className={barraClasse} style={barraCss}>
                        <div style={barraContainerCss} className='row'>
                            <svg width="270" height="60" viewBox="0 0 279 65" fill="none" xmlns="http://www.w3.org/2000/svg">
                                <path d="M-33.5 15C-33.5 6.99188 -27.0081 0.5 -19 0.5H264C272.008 0.5 278.5 6.99187 278.5 15V32.2285C278.5 40.2758 271.947 46.7838 263.9 46.7281L64.9944 45.3529C61.9799 45.332 59.0248 46.1907 56.4908 47.8237L32.6814 63.1678C34.6917 56.9797 30.4177 50.2592 23.6493 49.7403L-20.1084 46.3855C-27.6645 45.8062 -33.5 39.5061 -33.5 31.9279V15Z" fill="white" stroke="#C4C4C4" />
                            </svg>
                            <img src={iLinkedin} style={logoCss}></img><a  style={linkCss} className='ml-2' href='https://www.linkedin.com/in/alanreisb'>linkedin.com/in/alanreisb</a>
                        </div>
                    </div>

                    <div className={barraClasse} style={barraCss}>
                        <div style={barraContainerCss} className='row'>
                            <svg width="270" height="60" viewBox="0 0 279 65" fill="none" xmlns="http://www.w3.org/2000/svg">
                                <path d="M-33.5 15C-33.5 6.99188 -27.0081 0.5 -19 0.5H264C272.008 0.5 278.5 6.99187 278.5 15V32.2285C278.5 40.2758 271.947 46.7838 263.9 46.7281L64.9944 45.3529C61.9799 45.332 59.0248 46.1907 56.4908 47.8237L32.6814 63.1678C34.6917 56.9797 30.4177 50.2592 23.6493 49.7403L-20.1084 46.3855C-27.6645 45.8062 -33.5 39.5061 -33.5 31.9279V15Z" fill="white" stroke="#C4C4C4" />
                            </svg>
                            <img src={iGithubb} style={logoCss}></img>  <a style={linkCss} href='https://www.github.com/alanreisb'>github.com/alanreisb</a>
                        </div>
                    </div>

                    <div className={barraClasse} style={barraCss}>
                        <div style={barraContainerCss} className='row'>
                            <svg width="270" height="60" viewBox="0 0 279 65" fill="none" xmlns="http://www.w3.org/2000/svg">
                                <path d="M-33.5 15C-33.5 6.99188 -27.0081 0.5 -19 0.5H264C272.008 0.5 278.5 6.99187 278.5 15V32.2285C278.5 40.2758 271.947 46.7838 263.9 46.7281L64.9944 45.3529C61.9799 45.332 59.0248 46.1907 56.4908 47.8237L32.6814 63.1678C34.6917 56.9797 30.4177 50.2592 23.6493 49.7403L-20.1084 46.3855C-27.6645 45.8062 -33.5 39.5061 -33.5 31.9279V15Z" fill="white" stroke="#C4C4C4" />
                            </svg>
                            <img src={iWhatsapp} style={logoCss}></img>  <a style={linkCss} href='tel:(12)98250-1585' className='ml-2'>(12)98250-1585</a>
                        </div>
                    </div>
                </div>

            </div>
        );
    }
}

export default contatos;